@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);


*{
  box-sizing: border-box;
}

body{
  background-color: #111;
  color:white;
  margin: 0;
  font-family: 'Roboto', sans-serif;
}

.list{
  margin-top: -150px;
}

footer{
  margin:50px 0 ;
  text-align: center;
  color: aquamarine;
}

.loading{
  position: fixed;
  z-index: 99;
  left: 0;
  top: 0;
  bottom: 0;
  right:0;
  background: #000;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.movieRow{
    margin-bottom: 30px;
}

.movieRow h2{
    margin: 0px 0px 0px 30px;
}

.movieRow--listarea{
    overflow-x: hidden;
    padding-left: 30px;
}

.movieRow--list{
    transition: all ease 1.5s;
}


.movieRow--item{
    display: inline-block;
    width: 150px;
    cursor: pointer;
}

.movieRow--item img{
    width: 100%;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    transition: all ease .2s;
}

.movieRow--item img:hover{
    -webkit-transform: scale(1);
            transform: scale(1);
}

.navigation--left,.navigation--right{
    position: absolute;
    width: 40px;
    height: 225px;
    background: rgba(0,0,0,0.5);
    z-index: 99;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    overflow-x: hidden;
    cursor: pointer;
    opacity: 0;
    transition: all ease .2s;
}

.navigation--left{
    left:0;
}

.navigation--right{
 right:0;
}


.movieRow:hover .navigation--right,
.movieRow:hover .navigation--left{
    opacity: 1;

}

/* MEDIAQUERIES */

@media (max-width:760px){

.navigation--left, 
.navigation--right{
 opacity: 1;
 font-size: 25px;
}

}
.featured{
    height:100vh;
}

.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top,#111 10%, transparent 90%);
}

.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right,#111 30%, transparent 70%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;

}

.featured--name{
    font-size: 60px;
    font-weight: bold;
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
 
}

.featured--points, 
.featured--year, 
.featured--season{
    display: inline-block;
    margin-right: 15px;

}

.featured--points{
  
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    width: 40%;
}

.featured--buttons{
  margin-top: 5px;
}

.button--left,
.button--right{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.button--left{
    background: white;
    color:#000;
}

.button--right{
    background: #333;
    color:white;
}


.button--right:hover,.button--left:hover{
  opacity: 0.5;

}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

.playarrow{
    top: 5px;
    position: relative;
}

/* MEDIA QUERIES */

@media (max-width:760px){


    .featured{
        height: 90vh;
  
    }
    
    .featured--name{
        font-size: 40px;
    }
    
    .featured--info{
        font-size: 16px;
    }
    
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    
    .button--left,
    .button--right{
        font-size: 16px;
    }
    
    .featured--genres{
        font-size: 14px;
    
    }
    
    






}


header{
    position: fixed;
    z-index: 999;
    top:0;
    left:0;
    right:0;
    height: 70px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding:0 30px ;
    background: transparent;
    transition: all ease .5s;

}

header.black{
    background-color: #141414;
}

.header--logo {
    height: 25px;
}

.header--logo img{
    height: 100%;
}

.header--usuario {
    height: 35px;
}

.header--usuario img{
    height: 100%;
}

/* MEDIA QUERIES */

@media (max-width:769px){
    header {
        top: 0px;
        height: 90px;
}
}
