.featured{
    height:100vh;
}

.featured--vertical{
    width: inherit;
    height: inherit;
    background: linear-gradient(to top,#111 10%, transparent 90%);
}

.featured--horizontal{
    width: inherit;
    height: inherit;
    background: linear-gradient(to right,#111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 150px;
    padding-top: 70px;

}

.featured--name{
    font-size: 60px;
    font-weight: bold;
}

.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 15px;
 
}

.featured--points, 
.featured--year, 
.featured--season{
    display: inline-block;
    margin-right: 15px;

}

.featured--points{
  
    color: #46d369;
}

.featured--description{
    margin-top: 15px;
    font-size: 20px;
    color: #999;
    width: 40%;
}

.featured--buttons{
  margin-top: 5px;
}

.button--left,
.button--right{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    border-radius: 5px;
    text-decoration: none;
    margin-right: 10px;
    opacity: 1;
    transition: all ease 0.2s;
}

.button--left{
    background: white;
    color:#000;
}

.button--right{
    background: #333;
    color:white;
}


.button--right:hover,.button--left:hover{
  opacity: 0.5;

}

.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999;
}

.playarrow{
    top: 5px;
    position: relative;
}

/* MEDIA QUERIES */

@media (max-width:760px){


    .featured{
        height: 90vh;
  
    }
    
    .featured--name{
        font-size: 40px;
    }
    
    .featured--info{
        font-size: 16px;
    }
    
    .featured--description{
        font-size: 14px;
        max-width: 100%;
        margin-right: 30px;
    }
    
    .button--left,
    .button--right{
        font-size: 16px;
    }
    
    .featured--genres{
        font-size: 14px;
    
    }
    
    






}

